.navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: space-between;
    background-color: azure;
    align-items: center;
    z-index: 1000;
}
  
.navbar a {
   text-decoration-line: none;
   color: black;
}
  
.logo {
  padding: 0 20px;
  z-index: 2000;
}
  
.logo a {
  font-size: 40px;
  font-weight: 900;
  cursor: pointer;

  @media (max-width: 800px) {
    font-size: 30px;
  }
  }
  
.hamburger ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 0;
  padding-right: 50px;

  @media (max-width: 800px) {
    padding-right: 0px;
  }
}
  
.hamburger li {
  list-style: none;
  padding: 0 20px;
  font-size: 17px;
  font-weight: 900;
  color: black;
  cursor: pointer;
  z-index: 6000;
}