.Gallery {
    text-align: center;
}

* {
    background-color: azure;
}

.gallery-container {
    display: flex;
    justify-content: center;
    align-items: start;
    flex-wrap: wrap;
    width: 100%;
    max-width: 1200px;
    padding: 60px 0 0 0;
    color:rgba(187, 228, 238, 0.566);
}

h2 {
    width: 100%;
    display: block;
    text-align: center;
}

.picture-card {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 250px;
    width: 200px;
    margin: 0 auto;
    margin-left: 150px;
    margin-top: 50px;
    box-shadow: 0 0 10px rgb(166, 198, 198);
    border-radius: 10px;
    background-color: white;
}

.picture-card img {
    border-radius: 10px;
    height: 170px;
    margin-top: 15px;
    object-fit: cover;
    width: 170px;
    background-color: white;
}

.picture-card h4 {
    margin-top: 1px;
    margin-bottom: 5px;
    background-color: white;
}

.picture-card p{
    margin-top: 1px;
    margin-bottom: 5px;
    background-color: white;
}

.picture-card:hover {
    transform: translateY(-10px);
}